.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  gap: 10px;
  height: 100vh;
  justify-content: space-between;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Canvas styles */
.canvas {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  flex: 1;
  justify-content: center;
}

/* Info panel styles */
.info {
  max-height: 100vh;
  margin: 2px;
  flex: 0 0 20%;
  text-align: left;
  padding: 10px;
  background-color: #f5f5f5;
  border-left: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.info-content {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.info h3 {
  margin-top: 0;
  color: #333;
}

.info ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.info li {
  background-color: #dbdbdb;
  margin: 5px 0;
  padding: 8px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.info li:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.info li.selected {
  background-color: #cacaff;
}

.ruin-list-container {
  margin-top: 10px;
}

.ruin-list-container ul li { 
  font-size: 0.8em;
  font-weight: 600;
}

.path-list-container {
  margin-top: 10px;

}

.path-list-container ul summary, .path-list-container ul li{
  font-size: 0.8em;
  font-weight: 600;
}

#info details {
  padding: 5px;
}


.path:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.path {
  background-color: #dbdbdb;
}

.path-item {
  padding: 0.2em;
  background-color: #cacaff;
  display: block;
  position: relative;
}

.path-filter {
  background-color: #b49bff;
  padding: 0.2em;
  margin: 0.2em;
  padding: 0.2em;
  display: flex;
  align-items: center;
  font-size: 0.8em;
  font-weight: 600;
  justify-content: space-between;
}
.path-filter button {

  border-radius: 1em;
  border: none;
  background-color: #b49bff;
  color: white;
  font-size: 0.8em;
  font-weight: 600;
  cursor: pointer;
  height: 2em;
  width: 2em;
}

.path-filter button:hover {
  background-color: #9b80ff;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.large-title {
  font-size: 1.5em;
  font-weight: bold;
}

.help-overlay-icon {
  cursor: pointer;
  display: inline-block;
  position: absolute;
  color: #61dafb;
}

.help-overlay {
  position: relative;
  display: inline;
}

.help-overlay-icon:hover {
  scale: 1.5;
}

.help-overlay-content {
  padding: 1em;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 4px;
  z-index: 1;
  min-width: 200px;

  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0s linear 0.5s;

}


.overlay-visible {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s; 
}



.control-btn {
  margin-left: 1em;
  margin-top: 0.5em;
  padding: 0.5em 1em;
  border-radius: 0.5em;
  border: 1px solid #ddd;
  background-color: #f5f5f5;
  cursor: pointer;
  transition: background-color 0.2s;
  font-size: 0.8em;
}

.control-btn:hover {
  background-color: #e0e0e0;
}

.box-series-container {
  position: relative;
}

.box-series {
  display: flex;
  flex-direction: column;
  gap: 1em;
  position: absolute;
  background-color: #f5f5f5;
  padding: 1em;
  top: 0;
  left: -2em;
}

.box-series:hover {
  transform: scale(2) translateY(1.5em);
  transition: transform 0.2s;

}

.box {
  display: flex;
}

.box span:first-child {
  display: block;
  width: 10px;
  height: 10px;
}

.box-series .box span:nth-child(2) {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s, visibility 0.2s;
  text-align: left;
  width: 0;
  height: 0;
}

.box-series:hover .box span:nth-child(2) {
  opacity: 1;
  font-size: 0.1em;
  display: block;
  padding-left: 1em;
  visibility: visible;
  width: auto;
  height: auto;
}


.path-actions {

  padding:0.5em;
}

.path-actions-expand-icon {
  font-size: 1.5em;
  cursor: pointer;
  background-color: #b49bff;
  font-weight: 600;
  border-radius: 0.5em;
  width: 1em;
  height: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}


.path-actions-expand-icon span {

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.expand-info-btn {
  display: none;
  background-color: #b49bff;
  color: white;
  font-weight: 600;
  padding: 0 0.5em;
  border: none;
}

.expand-info-btn:hover {
  background-color: #9b80ff;
}

@media (max-width: 700px) {
  .App {
    flex-direction: column;
    display:flex;
  }

  .canvas {
    width: 100%;
    height: 100%;
  }

  .info {
    position: fixed;
    right: 0;
    display: flex;
    flex-direction: row;
  }

  .info.expanded {
    flex: 0 0 100%;
  }

  .info-content-collapsed {
    width: 0;
    visibility: hidden;
    transition: width 0.5s ease-in-out, visibility 0s linear 0.5s;
  }

  .expand-info-btn {
    display: flex;
    align-items: center;
    width: 2em;
    justify-content: center;
  }

  .item-20 {
    flex: initial; /* Assign a flex-grow factor of 20 */
  
  }
  
  .item-80 {
    flex: initial; /* Assign a flex-grow factor of 80 */
  
  }

  .box-series-container {
    display: none;
  }

  #myModal {
    z-index: 2;
  }
}

.used-ruin {
  opacity: 0.5;
}

.canvas-overlays {
  position: absolute;
  pointer-events: none;
  font-size: 0.5em;
}

.canvas-overlay-text p {
  max-width: 20em;
  display: block;
  width: 77px;
  font-weight: 600;
  background-color: #f5f5f5;
}

.info li.hover-ruin-container {
  background-color: #9898ff;

}

#myModal {
  pointer-events: none;
}

.toast {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f5f5f5;
  padding: 1em;
  border-radius: 0.5em;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s ease, visibility 0s linear 0.5s;
}

.toast--success {
  background-color: #a4ff66;
}

.toast--error {
  background-color: #ff6666;
}

.visible-toast {
  opacity: 1;
  visibility: visible;
}